import React, { Component } from "react";

import CustomCard from "./CustomCard";
import PoolCard from "./PoolCard";

import { projects } from "../../assets/variables";
import leftsidebigmamba from "../../assets/img/leftside_Big_mamba.svg";

export default class ProjectsOpen extends Component {
  render() {
    return (
      <>
        <section className="projects-open">
          <h3
            className="col-12 text-center font-weight-bold sub-title"
            style={{ marginTop: 70 }}
          >
            ACTIVE POOLS
          </h3>
          <section className="container custom-cards">
            {projects.map((project, index) => {
              return (
                project.status === "Open" && (
                  <CustomCard key={index} project={project} />
                )
              );
            })}
          </section>
          <div className="activepool-mamba">
            <img src={leftsidebigmamba} alt="leftsidebigmamba" />
          </div>
          <section className="container pool-cards">
            {projects.map((project, index) => {
              return <PoolCard key={index} project={project} />;
            })}
          </section>
        </section>
        <hr style={{ border: "1px solid #fdac42" }} />
      </>
    );
  }
}
