import React, { Component } from "react";

import CustomCard from "./CustomCard";

import { projects } from "../../assets/variables";
import leftmamba from "../../assets/img/leftside_mamba.svg";

export default class ProjectsClosed extends Component {
  render() {
    return (
      <>
        <section className="container projects-closed">
          <h3 className="col-12 text-center font-weight-bold sub-title">
            PROJECTS CLOSED
          </h3>
          <div className="closed-projects-mamba">
            <img src={leftmamba} alt="leftmamba" />
          </div>

          <section className="closed-projects">
            {projects.map((project, index) => {
              return (
                project.status === "Closed" && (
                  <CustomCard key={index} project={project} />
                )
              );
            })}
          </section>
        </section>
        <hr style={{ border: "1px solid #fdac42" }} />
      </>
    );
  }
}
