import React, { Component } from "react";
import { Container, Row, Col, Table, Card } from "react-bootstrap";
import Fade from "react-reveal/Fade";
import Zoom from "react-reveal/Zoom";

import FeatureCard from "./FeatureCard";

import { features } from "../../assets/variables";
import leftsidebigmamba from "../../assets/img/leftside_Big_mamba.svg";
import doughnut from "../../assets/img/doughnut.svg";

const MainFeatures = () => {
  return (
    <>
      <section className="container feature">
        <section className="feature-cards">
          <Zoom left>
            <div className="feature-mamba">
              <img src={leftsidebigmamba} alt="leftsidebigmamba" />
            </div>
          </Zoom>
          <Fade left>
            <h3 className="col-12 text-center font-weight-bold sub-title">
              MAIN FEATURES
            </h3>
            <p className="text-center">
              Introducing the amazing features of Mambapad
            </p>
          </Fade>
          <section className="projects">
            {features.map((feature, index) => {
              return <FeatureCard key={index} feature={feature} />;
            })}
          </section>
        </section>
        <section className="">
          <Zoom cascade>
            <Card className="tokenomics">
              <Row>
                <Col lg md="6" style={{display:'flex',flexDirection:'column', justifyContent:'space-around'}}>
                  <h3 className="col-12 text-center font-weight-bold feature-title">
                    TOKENOMICS
                  </h3>                  
                  <Table
                    className='taxtable'
                    bordered
                    hover
                    variant="dark"    
                  >
                    <thead>
                      <tr>
                        <th>TAX</th>
                        <th>BUY</th>
                        <th>SELL</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>HOLDERS</td>
                        <td>3%</td>
                        <td>6%</td>
                      </tr>
                      <tr>
                        <td>TEAM</td>
                        <td>3%</td>
                        <td>3%</td>
                      </tr>
                      <tr>
                        <td>TOTAL</td>
                        <td>6%</td>
                        <td>9%</td>
                      </tr>
                    </tbody>
                  </Table>
                </Col>
                <Col lg md="6" style={{ marginTop: "30px" }}>
                  <img src={doughnut} alt="doughnut" />
                </Col>
              </Row>
              <Row style={{ marginTop: "30px", padding:'10px', borderColor:'orange' }}>
                <Table
                  className='tokenomicstable'
                  bordered
                  hover
                  variant="dark"                  
                >
                  <thead>
                    <tr>
                      <th></th>
                      <th>TOKEN AMOUNT</th>
                      <th>%</th>
                      <th>VESTING (TGE)</th>
                      <th>VESTING(per MONTH)</th>
                      <th>CIRCULATIOIN TGE</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>TOTAL</td>
                      <td>$ 100,000,000</td>
                      <td>100</td>
                      <td>32</td>
                      <td>6</td>
                      <td>$ 32,000,000</td>
                    </tr>
                    <tr>
                      <td>TEAM</td>
                      <td>$ 5,000,000</td>
                      <td>5</td>
                      <td>40</td>
                      <td>10</td>
                      <td>$ 2,000,000</td>
                    </tr>
                    <tr>
                      <td>DEVELOPMENT</td>
                      <td>$ 5,000,000</td>
                      <td>5</td>
                      <td>40</td>
                      <td>10</td>
                      <td>$ 2,000,000</td>
                    </tr>
                    <tr>
                      <td>MARKETING</td>
                      <td>$ 5,000,000</td>
                      <td>5</td>
                      <td>40</td>
                      <td>10</td>
                      <td>$ 2,000,000</td>
                    </tr>
                    <tr>
                      <td>AIRDROP</td>
                      <td>$ 5,000,000</td>
                      <td>5</td>
                      <td>40</td>
                      <td>10</td>
                      <td>$ 2,000,000</td>
                    </tr>                    
                    <tr>
                      <td>PRIVATE</td>
                      <td>$ 10,000,000</td>
                      <td>10</td>
                      <td>60</td>
                      <td>10</td>
                      <td>$ 6,000,000</td>
                    </tr>
                    <tr>
                      <td>PRE-SALE</td>
                      <td>$ 30,000,000</td>
                      <td>30</td>
                      <td>60</td>
                      <td>10</td>
                      <td>$ 18,000,000</td>
                    </tr>
                    <tr>
                      <td>LIQUIDITY</td>
                      <td>$ 15,000,000</td>
                      <td>15</td>
                      <td></td>
                      <td></td>
                      <td></td>
                    </tr>                    
                    <tr>
                      <td>STAKE</td>
                      <td>$ 25,000,000</td>
                      <td>25</td>
                      <td></td>
                      <td></td>
                      <td></td>
                    </tr>                    
                  </tbody>
                </Table>
              </Row>
            </Card>
          </Zoom>
        </section>
      </section>
      <hr style={{ borderColor: "#f1b90c" }} />
    </>
  );
};

export default MainFeatures;
