import React from "react";
import {
    FaTwitter,
    FaTelegram,
    FaInstagram,
    FaLinkedinIn,
    FaYoutube,
    FaDiscord,
  } from "react-icons/all";

const SocialLink = () => {
  return (
    <div style={{ marginTop: "40px" }}>
      <FaTelegram
        size={30}
        style={{ marginRight: "20px", cursor: "pointer" }}
      />
      <FaTwitter size={30} style={{ marginRight: "20px", cursor: "pointer" }} />
      <FaDiscord size={30} style={{ marginRight: "20px", cursor: "pointer" }} />
      <FaInstagram
        size={30}
        style={{ marginRight: "20px", cursor: "pointer" }}
      />
      <FaLinkedinIn
        size={30}
        style={{ marginRight: "20px", cursor: "pointer" }}
      />
      <FaYoutube size={30} style={{ marginRight: "20px", cursor: "pointer" }} />
    </div>
  );
};

export default SocialLink;
